// Walls
const wallsMaterial = new THREE.MeshStandardMaterial({
    color: '#ffffff',
    roughness: 0.4,
    envMap: environmentMapTexture,
    side: THREE.DoubleSide
});
const wallGeometry = new THREE.PlaneGeometry(15, 5);

function roomWallConstractor(rotationY, position, width) {

    //threejs
    const roomWall = new THREE.Mesh(wallGeometry, wallsMaterial);
    roomWall.position.copy(position);
    roomWall.rotation.y = rotationY;
    roomWall.scale.set( width ? width : 1 , 1);
    roomWall.recieveShadow = true;
    scene.add(roomWall);

    //cannonjs
    const cannonShape = new CANNON.Plane();
    const body = new CANNON.Body({
        mass:0,
        shape: cannonShape,
        position: new CANNON.Vec3(0, 0, 0),
        material: defaultMaterial
    });
    body.quaternion.setFromAxisAngle(new CANNON.Vec3(0,-1,0), rotationY); // y axes
    body.position.copy(position);
    world.addBody(body);

}

roomWallConstractor(0, { x: 0, y: 2.5, z: -5});
roomWallConstractor((Math.PI / 2), { x: 7.5, y: 2.5, z: 0}, 2/3);
roomWallConstractor(-(Math.PI / 2), { x: -7.5, y: 2.5, z: 0}, 2/3);

