const giftboxMaterial = new THREE.MeshMatcapMaterial({ matcap: gifboxMatcap, side: THREE.DoubleSide });
const giftboxGeometry = new THREE.PlaneGeometry(3,1);
const giftboxFloorGeometry = new THREE.PlaneGeometry(3,3);

const giftBox = new THREE.Group();
scene.add(giftBox);

function giftboxWallConstractor(rotationY, position) {

    //threejs
    const boxWall = new THREE.Mesh(giftboxGeometry, giftboxMaterial);
    boxWall.position.copy(position);
    boxWall.rotation.y = rotationY;
    boxWall.castShadow = true;
    giftBox.add(boxWall);
    giftBox.position.set(3,0,-2); // Place the box in the room

    //cannonjs
    const cannonShape = new CANNON.Box(new CANNON.Vec3(giftboxGeometry.parameters.width/2, giftboxGeometry.parameters.height/2, 0.2));
    const body = new CANNON.Body({
        mass:0,
        shape: cannonShape,
        position: new CANNON.Vec3(0, 3, 0),
        material: defaultMaterial
    });

    body.quaternion.setFromAxisAngle(new CANNON.Vec3(0,-1,0), rotationY); // y axes
    body.position = new CANNON.Vec3(
        giftBox.position.x+boxWall.position.x,
        giftBox.position.y+boxWall.position.y,
        giftBox.position.z+boxWall.position.z
    );
    world.addBody(body);

}

