    // Font constractor
    function createText(text, material, size, position, rotationY) {
        fontLoader.load('/fonts/droid_serif_regular.typeface.json',
        function ( font ) {
            const textGeometry = new THREE.TextGeometry( text, {
                font: font,
                size: size,
                height: 0.1,
                curveSegments: 12,
                bevelEnabled: true,
                bevelThickness: 0.005,
                bevelSize: 0.005,
                bevelOffset: 0.005,
                bevelSegments: 1
            } );
            
    const textMesh = new THREE.Mesh(textGeometry, material);
    scene.add(textMesh);
    
    textGeometry.center();
    textMesh.position.copy(position);
    textMesh.rotation.y = rotationY;    
    });
    }

