/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
};

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
});

/**
 * Camera
 */

// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 1000);
camera.position.set(0, 1, 4);
scene.add(camera);
camera.lookAt(0,0,-1);


/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({ canvas: canvas, antialias: true });
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = THREE.PCFSoftShadowMap;
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
renderer.setClearColor("#eeeeee");


// Controls
const orbitControls = new OrbitControls(camera, canvas);
orbitControls.enableDamping = true;

const dragControls = new DragControls([ ... draggableObjects ], camera, renderer.domElement);
dragControls.enabled = true;


/**
 * Animate
 */
const clock = new THREE.Clock();
let oldElapsedTime = 0;

function animation() {
    const elapsedTime = clock.getElapsedTime();
    const deltaTime = elapsedTime - oldElapsedTime;
    oldElapsedTime = elapsedTime;

    //Update cannon world
    world.step(1 / 60, deltaTime, 3);

    if ( !sphere.draggedNow ) {
    sphere.position.copy(sphereBody.position); // Copy cannon sphere position to three js sphere
    } else {
        sphere.position.clamp(
        new THREE.Vector3(-7,(sphere.geometry.parameters.radius/2),-7),
        new THREE.Vector3(7,5,7)
        );
    };

    // Update controls
    orbitControls.update();

    // Call tick again on the next frame
    window.requestAnimationFrame(animation);

    // Render
    renderer.render(scene, camera);
}

animation();


/**
 * Events
 */

const cursorDiv = document.getElementById('cursor-div');


dragControls.addEventListener( 'hoveron', function ( event ) {
    orbitControls.enabled = false;
    cursorDiv.classList.add('pointer');
} );
dragControls.addEventListener( 'hoveroff', function ( event ) {
    orbitControls.enabled = true;
    cursorDiv.classList.remove('pointer');
} );

dragControls.addEventListener( 'dragstart', function ( event ) {
    event.object.draggedNow = true;
    cursorDiv.classList.remove('pointer');
    cursorDiv.classList.add('grabbing');
} );

dragControls.addEventListener( 'dragend', function ( event ) { 
    sphereBody.position.copy(sphere.position);
    event.object.draggedNow = false;
    cursorDiv.classList.remove('grabbing');
 } );


 /**
  * HTML UI
  */

 // Sidebar elements
  const menuIcon = document.getElementById('menu');
  const sidebar =  document.getElementById('About-sidebar');
  let sidebarOpen = false;

// checkout elements
  const checkoutButton = document.getElementById('checkoutbtn');
  const checkoutClose = document.getElementById('close');
  const checkoutPopup = document.getElementById('checkout');
  let checkoutOpen = false;

  const payNowBTN = document.getElementById('payNow');


  menuIcon.addEventListener('click', function() {
      if (!sidebarOpen) {
      sidebar.style.right = 0;
      menuIcon.classList.add("closeButton");
      sidebarOpen = true;
      } else {
        sidebar.style.right = '-600px';
        menuIcon.classList.remove("closeButton");
        sidebarOpen = false;
      }
    });

    checkoutClose.addEventListener('click', function() {
        checkoutPopup.style.bottom = '-100vh';
        checkoutOpen = false;
        removeCart();
      });

      checkoutButton.addEventListener('click', function() {
          if (!checkoutOpen) {
                createCart();
                checkoutOpen = true;
                document.getElementById('cart').classList.remove('hidden');
                document.getElementById('payment-details').classList.add('hidden');
                checkoutPopup.style.bottom = 0;
          } else {
                checkoutPopup.style.bottom = '-100vh';
                checkoutOpen = false;
                removeCart();
          }

      });

      payNowBTN.addEventListener('click', function() {
        document.getElementById('cart').classList.add('hidden');
        document.getElementById('payment-details').classList.remove('hidden');
      });

      document.querySelectorAll('.pointerC').forEach(item => {
        item.addEventListener('mouseover', event => {
            cursorDiv.classList.add('pointer');
        })
      })

      document.querySelectorAll('.pointerC').forEach(item => {
        item.addEventListener('mouseout', event => {
            cursorDiv.classList.remove('pointer');
        })
      })

    document.addEventListener('mousemove', function(mouse) {
        cursorDiv.style.left =  mouse.clientX + 'px';
        cursorDiv.style.top =  mouse.clientY + 'px';
    });


    function createCart() {
        const itemsInBox = [];
        let totalCost = 0;

        const boxCenterX = giftBox.position.x;
        const boxCenterZ = giftBox.position.z;
        const distanceToEdge = giftboxFloorGeometry.parameters.width / 2;

        // check if draggable objects is in the box
        for(var i = 0; i < draggableObjects.length; i++ ) {
            if ( draggableObjects[i].position.x < boxCenterX+distanceToEdge &&
                draggableObjects[i].position.x > boxCenterX-distanceToEdge &&
                draggableObjects[i].position.z < boxCenterZ+distanceToEdge &&
                draggableObjects[i].position.z > boxCenterZ-distanceToEdge) {

                    const liElement = document.createElement("li");
                    const nameSpan = document.createElement("span");
                    nameSpan.append(draggableObjects[i].cartName);
                    const priceSpan = document.createElement("span");
                    priceSpan.append(draggableObjects[i].price + "$");
                    liElement.append(nameSpan, priceSpan);

                    document.getElementById('itemlist').append(liElement);

                    totalCost =+ draggableObjects[i].price; //total price of cart
            }
        }

        document.getElementById('totalPrice').innerHTML = totalCost + "$";
        
    }
    
    function removeCart () {
        let totalCost = 0;
        document.getElementById('itemlist').innerHTML = " ";
    }
    